import React from 'react';
import { withStyles } from '@material-ui/core/styles';
import PropTypes from 'prop-types';
import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Grid from '@material-ui/core/Grid';
import Card from '@material-ui/core/Card';
import CardActionArea from '@material-ui/core/CardActionArea';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Button from '@material-ui/core/Button';
import Typography from '@material-ui/core/Typography';
import CloudDownloadIcon from '@material-ui/icons/CloudDownload';

const styles = theme => ({
    root: {
        flexGrow: 1,
    },
    card: {
        maxWidth: 300,
    },
    media: {
        height: 140,
    },
});

const tasks = [
    {
        "groupDisplayName": "FTP",
        "tasks": [
            {
                "displayName": "FTP List",
                "description": "List files on a sFTP or on a FTP serveur.",
                "image": "/public/data-factory/tasks/spredsheet.png"
            },
            {
                "displayName": "FTP Get",
                "description": "Get one file on a sFTP or on a FTP serveur.",
                "image": "/public/data-factory/tasks/spredsheet.png"
            },
            {
                "displayName": "FTP Post",
                "description": "Post one or many files on a sFTP or on a FTP serveur.",
                "image": "/public/data-factory/tasks/spredsheet.png"
            }
        ]
    }
    
];


function Tasks(props) {
    const { classes } = props;
    return (
        <React.Fragment>
            <CssBaseline />
            <div className={classes.root}>
                <Container maxWidth="lg">
                    <h3>FTP</h3>
                    <Grid container spacing={3}>
                        <Grid item xs={4}>
                            <Card className={classes.card}>
                                <CardActionArea>
                                    <CardMedia
                                        className={classes.media}
                                        image="/static/images/cards/contemplative-reptile.jpg"
                                    />
                                    <CardContent>
                                        <Typography gutterBottom variant="h5" component="h2">
                                            FTP List
                                        </Typography>
                                        <Typography variant="body2" color="textSecondary" component="p">
                                            List files on a sFTP or on a FTP serveur. 
                                        </Typography>
                                    </CardContent>
                                </CardActionArea>
                                <CardActions>
                                    <Button size="small" color="primary">
                                        Learn More
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                        <Grid item xs={4}>
                            FTP get
                        </Grid>
                        <Grid item xs={4}>
                            FTP get
                        </Grid>
                    </Grid>
                </Container>
            </div>
        </React.Fragment>
    )
}

Tasks.propTypes = {
    classes: PropTypes.object.isRequired,
};

export default withStyles(styles)(Tasks);